.wrapper {
  position: relative;
  display: grid;
  grid-template-columns: 100%;
  grid-template-rows: 1fr;

  background: var(--color-secondary);
  border-radius: 10px;

  overflow-y: scroll;
}

@media (max-width: 1399px) {
  .wrapper {
    margin-right: 15px;
  }
}

@media (max-width: 767px) {
  .wrapper {
    margin: 15px 15px 0 15px;
  }
}

@media (max-width: 575px) {
  .wrapper {
    margin: 10px 10px 0 10px;
  }
}
